import React from 'react';
import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getDogs, listOfTemperaments } from '../actions/index';    
import { Link } from 'react-router-dom';
import Card from './Card';
import Pagination from './Pagination';
import SearchBar from './SearchBar';
import Nav from './Nav';


export default function Home() {
    const dispatch = useDispatch();             // mapDispatchToProps para Hooks. Crea una instancia de la funci√≥n para despachar actions.
    const allDogs = useSelector ((state) => state.dogs);     // mapStateToProps para Hooks. Retorna los datos de la store que se necesiten.
    

    /* Paginado */
    const [currentPage, setCurrentPage] = useState(1);              //pagina actual
    const [dogsPerPage] = useState(8);                              //8 perros por p√°gina
    const indexOfLastDog = currentPage * dogsPerPage;               //√≠ndice √∫ltimo perro
    const indexOfFirstDog = indexOfLastDog - dogsPerPage;           //√≠ndice primer perro
    const currentDog = allDogs.slice(indexOfFirstDog, indexOfLastDog);  //Perros de la p√°gina actual  P√°g.1 ---> 0-7

    const paginate = (pageNumber) => {
        setCurrentPage(pageNumber)
    }
    
    useEffect (() => {                                      
        dispatch(listOfTemperaments());
        dispatch(getDogs());
    }, [dispatch]);
//------------------------------------------------------------------------------------------
    function handleClick(e){
        e.preventDefault();
        dispatch(getDogs());
    }


    return(
        <div>
            <div>
                <Link to='/dog'>
                    <button>
                        <div>
                            <div>
                            Crea tu propia raza
                            </div>
                        </div>
                    </button>
                </Link>
            </div>
            <div>

                <button onClick={e => handleClick(e)}>Volver a cargar</button>
            </div>
            <div className='search'>
                <SearchBar />
            </div>
            <div className='navig'>
                <Nav />
            </div>
            <div className='pagin'>
                <Pagination
                    dogsPerPage={dogsPerPage}
                    allDogs={allDogs.length}
                    paginate={paginate}
                />
            </div>
            <div>
                {currentDog.length > 0 ? (
                    currentDog.map(e => {
                        return (
                            <Card 
                                key={e.id}
                                id={e.id}
                                name={e.name} 
                                image={e.image} 
                                temperament={`Temperaments: ${e.temperament}`}
                                weightMin={`Weight Min: ${e.weightMin} kg.`}
                                weightMax={`Weight Max: ${e.weightMax} kg.`}
                            />
                            );
                        })
                ) : (
                        <div>
                            
                        </div>     
                    )}
            </div>
        </div>
    );
}