import React from "react";

export default function Card({id, image, name, temperament, weightMin, weightMax}){
    return(
        <div>
            <h2>{name}</h2>
            <img className='image' src={image} alt='img not found' width='250px' height='200px'/>
            <p>{temperament}</p>
                <p>{weightMin}</p>
                <p>{weightMax}</p>
        </div>
    )
}